}
.Movie__Background {
    position: relative;
    padding-bottom: 42%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}
.Movie__Background:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 10%;
    background: -webkit-gradient(linear, left bottom, left top, from(#1d1d27), to(transparent));
    background: -webkit-linear-gradient(bottom, #1d1d27, transparent);
    background: linear-gradient(to top, #1d1d27, transparent);
}

